body{
    padding-left: 200px;
    padding-right: 200px;
    padding-top: 20px;
    background-attachment: fixed;
   background-color:azure;

}

.card-container {
    display: flex;
    flex-direction: column;
  }
  
  .product-item {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 250px; 
  }
  
  .product-card {
    width: 100%;
    height: 100%;
    background-color: #f9f9f9;
  }
  
  .empty-card {
    width: 100%;
    height: 100%;
    background-color: #e0e0e0; 
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .empty-card .MuiCardContent-root {
    text-align: center;
  }
  .heading{
    
    text-align: center;
  
  }
  .btn{
    display: flex;
    justify-content: space-between;
  }
  